<template>
  <div class="login">
    <Panel>
      <template #header>
        <router-link to="/">首页</router-link>
        <span class="c">/</span>
        <span class="c">登录</span>
      </template>
      <template #content>
        <el-form
          :model="ruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="100px"
          hide-required-asterisk
        >
          <el-form-item label="用户名:" prop="username">
            <el-input
              placeholder="用户名任意"
              type="text"
              v-model="ruleForm.username"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="密码:" prop="password">
            <el-input
              placeholder="输入用户token"
              type="passwordword"
              v-model="ruleForm.password"
              autocomplete="off"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitForm('ruleForm')"
              >登录</el-button
            >
            <el-button @click="resetForm('ruleForm')">重置</el-button>
          </el-form-item>
        </el-form>
      </template>
    </Panel>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
import Panel from "../containers/Panel.vue";
export default {
  components: { Panel },
  data() {
    return {
      ruleForm: {
        username: "wl",
        password: "51fde48d-8b01-4935-996a-92aa5d2f9e90",
      },
      rules: {
        username: { required: true, message: "请输入用户名", trigger: "blur" },
        password: { required: true, message: "请输入密码", trigger: "blur" },
      },
    };
  },
  computed: {
    ...mapState({
      info: (state) => state.userInfor.info,
    }),
  },
  methods: {
    submitForm() {
      this.$refs.ruleForm.validate(async (valid) => {
        if (valid) {
          this.getInfor(this.ruleForm.password).then(
            (res) => {
              this.$message.success("登录成功");
              this.$router.push("/");
            },
            (error) => {
              this.$message.error("token错误");
            }
          );
        }
      });
    },
    resetForm() {},
    ...mapActions(["getInfor"]),
  },
};
</script>

<style lang="less">
.login {
  a {
    color: #1998fb;
  }
  .c {
    color: #000;
    margin-left: 8px;
  }
  .el-form {
    margin-left: 100px;
    .el-input {
      width: 300px;
    }
  }
}
</style>
